import { SeoElements } from './../../../core/constants';
import { MixpanelService } from './../../../core/mixpanelService.service';
import { GalleryviewpopupComponent } from './../../../galleryviewpopup/galleryviewpopup.component';
import { LocalStorageService } from './../../../core/service/local-storage.service';
import { GymBranchDistinct, GymBranchResponseList, TrainerBranchResponseList } from './../../../core/models/trainerDetailResponse';
import { MediaResponseDtoList } from '../../../core/models/mediaResponseList';
import { ModelpopupLrComponent } from './../../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { UserSession } from '../../../core/sessions';
import { StorageService } from './../../../core/service/storage.service';
import { HttpUserAuthUtilsService } from './../../../core/http_wrapper/http-userauth-utils.service';
import { HttpUserUtilsService } from './../../../core/http_wrapper/http-user-utils.service';
import { UserLogin } from './../../../core/models/userloginresponse';

import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { HttpSuccesFailureResponse } from '../../../core/http_wrapper/http_wrapper_response';
import { TrainerDetail } from '../../../core/models/trainerDetailResponse';
import { Constants, MixpanelEvent } from '../../../core/constants';
import { Meta } from '@angular/platform-browser';
declare var $: any;





@Component({
  selector: 'app-trainerpersonaldetails',
  templateUrl: './trainerpersonaldetails.component.html',
  styleUrls: ['./trainerpersonaldetails.component.css']
})
export class TrainerpersonaldetailsComponent implements OnInit, HttpSuccesFailureResponse {
  @Output() coverImage = new EventEmitter();
  @Output() profileImage = new EventEmitter();
  shareurl: string;
  userdata: UserLogin;
  trainerDetail: TrainerDetail;
  activitieslist: string[] = [];
  trainerMediaDtoList: MediaResponseDtoList[];
  associatedBranchList: TrainerBranchResponseList[] = [];
  gymBranchDistinct: GymBranchDistinct[] = [];
  gymListMap: Map<number, GymBranchDistinct[]> = new Map<number, GymBranchDistinct[]>();
  userType: any;
  trainerId: any;
  foundtrainer= false;
  trainerName: string;
  public isPrime = UserSession.getDynamicElements(Constants.isPrime)=="Yes"?true:false;



  constructor(private http_user: HttpUserUtilsService, private http_userauth: HttpUserAuthUtilsService,
     private storageService: StorageService, private router: Router, private meta: Meta,
     private mixpanel: MixpanelService, private modal: Modal, private _activatedRoute: ActivatedRoute) { }

  ngOnInit() {

    if (this.isLoggedIn()) {
      this.userdata = this.storageService.read<UserLogin>(Constants.userDataKey);
    }
    this.getTrainerprofile();
    this.userType = LocalStorageService.getUserType()

  }
    handleScroll() {
        console.clear();
        window.onscroll = () => {
            let element = document.getElementById('sticky');
            // var offset = element.offsetTop;
            if (window.pageYOffset >= 20) {
                element.classList.add('affix')
            } else {
                element.classList.remove('affix');
            }
            let footer = document.getElementById('pageFooter');

        }
    }
  getTrainerprofile() {
    this._activatedRoute.params.subscribe(params => {
      if (params['id']) {
        this.trainerId = params['id'];
        this.http_userauth.getTrainerProfileDetail(params['id'], this, true);
      } else {
        if (sessionStorage.getItem('trainer_id')) {
          this.trainerId = sessionStorage.getItem('trainer_id')
          this.http_userauth.getTrainerProfileDetail(sessionStorage.getItem('trainer_id'), this, true);
        } else {
          this.router.navigate(['/'])
        }
      }
    });
  }
  isLoggedIn() {
    return UserSession.isLoggedIn();
  }
  getuserdata(): UserLogin {
    try {
      return this.storageService.read<UserLogin>(Constants.userDataKey);
    } catch (e) {
      return null;
    }
  }
  MarkTarinerAsFavorite(trainerName: string) {
    if (localStorage.getItem(Constants.authToken)) {
      this.userdata = this.getuserdata();
      this.trainerName = trainerName;
      this.http_userauth.markTrainerAsFavourit(this.trainerId, LocalStorageService.getUserId(), this, true);

    } else {
      return this.modal.open(ModelpopupLrComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));

    }
  }
  MarkTrainerAsUnFavorite(trainerName: string) {
      this.trainerName = trainerName;
       this.http_userauth.markTrainerAsUnFavourit(this.trainerId, LocalStorageService.getUserId(), this, true);

  }
  callmixpanel() {
    this.mixpanel.logEventWithAnalytics(MixpanelEvent.eventOnTrainerProfile, {'Source' : 'web' , 'action' : 'looking_gallery'})
  }
    onSuccess(type: any, responsedata: any) {

    if (type == Constants.FindTrainerProfile) {
      this.activitieslist = [];
      this.trainerDetail = responsedata;
      this.coverImage.next(this.trainerDetail.coverImage);
      this.profileImage.next(this.trainerDetail.profileImage);
      this.shareurl = UserSession.getDynamicElements(Constants.ShareUrl) + 'trainer/' + this.trainerDetail.id + '/' +
      this.trainerDetail.firstName.replace(/[(+{})]/g, '').replace(/ /g, '');
      this.foundtrainer = true;
      if (this.trainerDetail.classCategoryList) {
       this.trainerDetail.classCategoryList.forEach(element => {
        this.activitieslist.push(element.classCategoryName);
      });
    }
      this.associatedBranchList = this.trainerDetail.trainerBranchResponseList;
      this.setupGyminfoOFTrainer(this.associatedBranchList)
 this.trainerMediaDtoList = this.trainerDetail.mediaResponseDtoList;
        setTimeout(() => {
            window['$']('.gallery a').simpleLightbox();
        }, 100);

    } if (type === Constants.MarkFavouriteTrainer) {
      this.mixpanel.logEventWithAnalytics(MixpanelEvent.addFavorite, { 'origin': 'Trainer_Profile', 'Source' : 'web' ,
       'Trainer_Name' : this.trainerName })
      this.getTrainerprofile();

    }
    if (type === Constants.MarkTrainerAsUnFavrourite) {
      this.mixpanel.logEventWithAnalytics(MixpanelEvent.removeFromFavourite, { 'origin': 'Trainer_Profile' , 'Source' : 'web' ,
      'Trainer_Name' : this.trainerName})
      this.getTrainerprofile();

    }
  }
  onFailure(type: any, response: string) {
    if (type == Constants.FindTrainerProfile) {
    }
    if (type == Constants.MarkFavouriteTrainer) {

    }
  }
    setupGyminfoOFTrainer(associatedBranchList: TrainerBranchResponseList[]) {
    const distinctgymid: number[] = [];
associatedBranchList.forEach(element => {
           if (distinctgymid.indexOf(element.gymId) == -1) {
             distinctgymid.push(element.gymId);
           }
       });
distinctgymid.forEach(element => {
  const myarray: GymBranchDistinct[] = [];
  this.gymBranchDistinct = [];
for (let i = 0; i < associatedBranchList.length; i++) {
    if (element == associatedBranchList[i].gymId) {
      myarray.push({'gymName': associatedBranchList[i].gymName, "gymId": associatedBranchList[i].gymId, "localitybranchName": associatedBranchList[i].localityName});
    }
}
this.gymListMap.set(distinctgymid.indexOf(element) + 1, myarray);
});

 this.gymListMap.forEach(element => {
   const gymDetails: string[] = [];
   element.forEach(element => {
     gymDetails.push(element.localitybranchName);
   });

  this.gymBranchDistinct.push({'gymName': element[0].gymName, "gymId": element[0].gymId, "localitybranchName": gymDetails.join(' ,')})
 });
}
gallerryPopup(index) {
   const mediaList = this.trainerDetail.mediaResponseDtoList;
      return this.modal.open(GalleryviewpopupComponent, overlayConfigFactory({mediaList, index}, BSModalContext));

  }
     navigatetoGym(value) {
       if (this.userType != 3) {
    sessionStorage.setItem('gym_id', value)
    this.mixpanel.logEventWithAnalytics(MixpanelEvent.visitGymProfile, {'origin': 'Trainer_Profile' , 'Source' : 'web'});
    this.router.navigate(['gym', value, Date.now()])
       }
  }
  copytext() {
    let $temp = $('<input>');
    $('body').append($temp);
    $temp.val($('#myinput').val()).select();
    document.execCommand('copy');
    $temp.remove();
}

}



