import { Meta } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { LocalStorageService } from './../../core/service/local-storage.service';
import { Component, OnInit } from '@angular/core';
import { Constants, SeoElements } from '../../core/constants';
import { UserSession } from 'core/sessions';
import { FBPixel } from 'fbpixelCustomEvents';

@Component({
  selector: 'app-trainerdetailsmodule',
  templateUrl: './trainerdetailsmodule.component.html',
  styleUrls: ['./trainerdetailsmodule.component.css']
})
export class TrainerdetailsmoduleComponent implements OnInit {
  profileImage: any;
  coverImage: any;
  public isPrime = UserSession.getDynamicElements(Constants.isPrime)=="Yes"?true:false;

  constructor(private router: Router, private meta: Meta) {
    this.meta.updateTag({name: SeoElements.title , content: SeoElements.userTrainerMetaTitle})
    this.meta.updateTag({name: SeoElements.description , content: SeoElements.userTrainerDescription})
    this.meta.updateTag({name: SeoElements.title, content: SeoElements.userTrainerKeywords})
    
  }

  ngOnInit() {
    FBPixel.trackPageView();

    // if(LocalStorageService.getUserType()==2){
    // this.router.navigate(['/'])
    // }
  }
  getCoverImage(event) {
    this.coverImage = event;
  }
  getProfileImage(event) {
    this.profileImage = event;
  }

}



