import { GlobalSubscriptionService } from './../../../app/globalSubcription.service';
import { ClassDetail } from 'core/models/classListResponse';
import { UserSession } from './../../../core/sessions';
import { ModelpopupLrComponent } from './../../../loginRegister/modelpopup-lr/modelpopup-lr.component';
import { JoinclasspopupComponent } from './../../../joinclasspopup/joinclasspopup.component';
import { Modal, BSModalContext } from 'angular2-modal/plugins/bootstrap';
import { element } from 'protractor';
import { LocalStorageService } from '../../../core/service/local-storage.service';
import { HttpUserUtilsService } from '../../../core/http_wrapper/http-user-utils.service';
import { Constants, InfiniteScroll } from './../../../core/constants';
import { HttpSuccesFailureResponse } from './../../../core/http_wrapper/http_wrapper_response';
import { Overlay, overlayConfigFactory } from 'angular2-modal';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { EditclasspopupComponent } from "editclasspopup/editclasspopup.component";
import { Router, ActivatedRoute } from '@angular/router';
import { HttpUserAuthUtilsService } from 'core/http_wrapper/http-userauth-utils.service';
import { ConfirmpopupComponent } from 'common/confirmpopup/confirmpopup.component';



@Component({
  selector: 'app-upcomingclasses',
  templateUrl: './upcomingclasses.component.html',
  styleUrls: ['./upcomingclasses.component.css']
})
export class UpcomingclassesComponent implements OnInit, HttpSuccesFailureResponse {
  upcommingClaasesDetail: ClassDetail[] = [];
  upcommingClassCount: number = 0;
  @Output() updatecount = new EventEmitter();
  currentPage: number = 0;
  isLoadMoreData: boolean = true;
  lastScrollDistance: number = 0;
  listIsLaoding: boolean = false;
  userType: any;
  refreshRequest: boolean = false;
  classTimingId: number = 0;
  trainerId: any;
  isLoaded: boolean = false;
  platformNameString:any;
  currency:any;
  public atGymText = UserSession.getDynamicElements(Constants.AtGymText)
  wishListClassName: string;
  wishListClassSessionId: any;
  public throttle = InfiniteScroll.throttle;
  public scrollDistance = InfiniteScroll.scrollDistance;
  public scrollUpDistance = InfiniteScroll.scrollUpDistance;
  public isPrime = UserSession.getDynamicElements(Constants.isPrime)=="Yes"?true:false;



  constructor(private http_userauth: HttpUserAuthUtilsService,private globalSubscriptionService: GlobalSubscriptionService, private http_user: HttpUserUtilsService, private modal: Modal, private router: Router, private _activatedRoute: ActivatedRoute) {

  }


  ngOnInit() {
    this.currency = LocalStorageService.getCurrencyCode();

    const that = this;

    this.listIsLaoding = true;
    this.userType = LocalStorageService.getUserType();
    this.gettrainerprofile();
    this.globalSubscriptionService.ClassListState
      .subscribe((value) => {
        this.refreshList(value);
      });
      this.globalSubscriptionService.waitListState
      .subscribe((value) => {
        this.refreshListWait(value);
      });

  }
  gettrainerprofile() {
    this._activatedRoute.params.subscribe(params => {
      if (params["id"]) {
        this.trainerId = params["id"];
        this.http_user.getTrainerUpcommingClasses(this.trainerId, this.currentPage, this, true);
      }
      else {
        if (sessionStorage.getItem("trainer_id")) {
          this.trainerId = sessionStorage.getItem("trainer_id")
          this.http_user.getTrainerUpcommingClasses(this.trainerId, this.currentPage, this, true);
        }
        else {
          this.router.navigate(['/'])
        }
      }
    });
  }

  getPlatformNameString(platform){
    return Constants.getPlatformNameStringFunction(platform);
  }

  onSuccess(type: any, responsedata: any) {
    if (type == Constants.TrainerUpcomingClassesApi) {
      this.listIsLaoding = false;
      var temp_list: ClassDetail[] = responsedata.data;
      if (this.currentPage == 0 ) {
        this.upcommingClaasesDetail = [];
        try{
          this.upcommingClassCount = temp_list[0].count;
          this.updatecount.next(this.upcommingClassCount);
        }catch(e){
          this.upcommingClassCount = 0;
          this.updatecount.next(this.upcommingClassCount);

        }
       

      }
      temp_list.forEach(element => {
        element.isSpecialProgram = UserSession.isSpecialProgramClass(element);
        this.upcommingClaasesDetail.push(element);
      });
      if (temp_list.length === 0) {
        this.isLoadMoreData = false;
      } else {
        this.isLoadMoreData = true;
      }

      // this.upcommingClassCount=this.upcommingClaasesDetail.length;
      // this.updatecount.next(this.upcommingClaasesDetail.length);
    }
    if (type === Constants.AddToWaitList) {
  
      this.openconfirmpopup(LocalStorageService.getRefundValue().waitListPopUpMessage, 'Success');
    
      this.globalSubscriptionService.onWaitListListRefresh(this.wishListClassSessionId);
    }
  }
  openconfirmpopup(message, title) {
    return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title }, BSModalContext));
  }
  onFailure(type: any, response: string) {
    if (type == Constants.TrainerUpcomingClassesApi) {
      this.listIsLaoding = false;
      if (this.currentPage == 0) {
        this.updatecount.next(this.upcommingClassCount);
      }

    }
  }
  openparticipantslist(gymSessionObj: ClassDetail) {
    if (this.userType != 1 ) {
      return this.modal.open(EditclasspopupComponent, overlayConfigFactory({ gymSessionObj }, BSModalContext))
    }
  }
  getdate(year, month, day): string {
    return year + "-" + month + "-" + day;
  }

  joinClass(classdetail, i) {
    
    if(classdetail.availableSeat==0&&this.userType == 1 &&!classdetail.userJoinStatus){
      this.addtowaitlist(classdetail);
      return;


    }
    this.classTimingId = i;
    classdetail.origin = 'Trainer_Profile';
      if (this.userType != 3) {
        return this.modal.open(JoinclasspopupComponent, overlayConfigFactory({ classdetail }, BSModalContext));

      } else {
        this.openparticipantslist(classdetail);
      }


   
  }
  addtowaitlist(classdetail: ClassDetail) {
    if (this.isLoggedIn()) {
      this.wishListClassName = classdetail.title;
      this.wishListClassSessionId = classdetail.classSessionId;
      if(!classdetail.userJoinStatus){
         
        if(LocalStorageService.getUser()!=null&&LocalStorageService.getUser().data!=null&&!LocalStorageService.getUser().data.mobile){
          let message  = "You need to add your mobile number before adding the class to waitlist.";
          let title = "Add Phone";
          let parent = this;
          let errorCode = "WAITLISTERR"
          return this.modal.open(ConfirmpopupComponent, overlayConfigFactory({ message, title , parent, errorCode}, BSModalContext));
  
        }

      this.http_userauth.addClassToWaitList(LocalStorageService.getUserId(), classdetail.classSessionId, this, true);
    }
    } else {
      return this.modal.open(ModelpopupLrComponent, overlayConfigFactory({ num1: 2, num2: 3 }, BSModalContext));

    }
  }
  isLoggedIn() {
    return UserSession.isLoggedIn();
  }
  onScrollDown() {
    if (this.isLoadMoreData && !this.listIsLaoding) {
      this.listIsLaoding = true;
      this.currentPage = this.currentPage + 1;
      if (sessionStorage.getItem("trainer_id")) {
        this.http_user.getTrainerUpcommingClasses(sessionStorage.getItem("trainer_id"), this.currentPage, this, false);
      }
      else {
        this._activatedRoute.params.subscribe(params => {
          if (params["id"]) {
            this.trainerId = params["id"];
            this.http_user.getTrainerUpcommingClasses(this.trainerId, this.currentPage, this, true);
          }
        });
        }
    }
    }
    onUp(){
      
    }
    resetBeforeLoad() {
      this.currentPage = 1;
      this.isLoadMoreData = true;
      this.upcommingClaasesDetail = [];

    }

    refreshList(value) {
      try {

        for(let i=0;i<this.upcommingClaasesDetail.length;i++){
          if(this.upcommingClaasesDetail[i].classSessionId == value){
            this.upcommingClaasesDetail[i].userJoinStatus = true;
            this.upcommingClaasesDetail[i].availableSeat = this.upcommingClaasesDetail[i].availableSeat - 1;
            break;
          }
        }
       
      }
      catch (e) {

      }

    }
    refreshListWait(value) {
      try {
      for (let i = 0; i < this.upcommingClaasesDetail.length; i++) {
          if (this.upcommingClaasesDetail[i].classSessionId == value) {
            this.upcommingClaasesDetail[i].wishType = 'WAITLIST';
            break;
          }
        }
  
    } catch (e) {
      console.log(e)
    }
    }
    navigateto(value) {
      if (LocalStorageService.getUserType() == 1) {
        sessionStorage.setItem("gym_id", value.gymId);
        sessionStorage.setItem("branch_id", value.branchId);
        this.router.navigate(['gym', value.gymId, value.gymName.replace(/ /g, '')])
      }
    }

  }




